@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap);
html,body{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #43454C;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
body::-webkit-scrollbar{
    display: none;
    background-color: #5cc1ac;
    color : #4f4e53;
}
body .App{margin:2rem;border-radius:3rem;background-color:rgba(255,255,255,0.664);overflow:hidden;display:flex;color:white}body .App-nav{background-color:#222529;flex-basis:5%;width:-webkit-max-content;width:-moz-max-content;width:max-content}body .App-nav>div{border-radius:3rem}@media only screen and (max-width: 1510px){body .App-nav{display:none}}@media only screen and (max-width: 1665px){body .App{margin:1rem}}@media only screen and (max-width: 1570px){body .App{margin:0rem;border-radius:0rem}}@media only screen and (max-width: 1450px){body .App{flex-direction:column;margin:2rem 11vw;border-radius:3rem}}@media only screen and (max-width: 1270px){body .App{margin:2rem 7vw}}@media only screen and (max-width: 1150px){body .App{margin:2rem 4vw}}@media only screen and (max-width: 1045px){body .App{margin:1rem 2vw}}@media only screen and (max-width: 1000px){body .App{margin:0;border-radius:0rem}}body .App-maindata{background-color:#282B2E;flex-basis:75%;padding:2.5rem;display:flex;flex-direction:column;justify-self:start;z-index:1000;grid-gap:2rem}@media only screen and (max-width: 450px){body .App-maindata{padding:1.5rem}}@media only screen and (max-width: 380px){body .App-maindata{padding:0.9rem}}body .App-tracks{background-color:#222529;z-index:20000;flex-basis:20%;display:flex;flex-direction:column;padding:2.5rem;grid-row-gap:1rem;flex-shrink:0}@media only screen and (max-width: 1450px){body .App-tracks{flex-direction:row;align-items:flex-end;grid-gap:4rem}}@media only screen and (max-width: 840px){body .App-tracks{grid-gap:2rem}}@media only screen and (max-width: 800px){body .App-tracks{flex-direction:column;align-items:center;justify-items:stretch}}@media only screen and (max-width: 450px){body .App-tracks{padding:1.5rem}}@media only screen and (max-width: 380px){body .App-tracks{padding:0.9rem}}

.Navbar{margin:3rem 0}.Navbar-svg{width:2rem;height:2rem;margin:0 auto;padding:0.5rem;border-radius:0.5rem;box-shadow:-3px 2px 7px rgba(0,0,0,0.301),3px 2px 7px rgba(0,0,0,0.199)}.Navbar-svg svg{width:2rem;height:2rem;fill:#f6c879}

.Spinner{font-size:10px;margin:50px auto;text-indent:-9999em;border-radius:50%;background:#f6c879;background:linear-gradient(to right, #f6c879 10%, rgba(246,200,121,0) 42%);position:relative;-webkit-animation:load3 1.4s infinite linear;animation:load3 1.4s infinite linear;transform:translateZ(0)}.Spinner:before{width:50%;height:50%;background:#f6c879;border-radius:100% 0 0 0;position:absolute;top:0;left:0;content:''}.Spinner:after{background:#282b2e;width:75%;height:75%;border-radius:50%;content:'';margin:auto;position:absolute;top:0;left:0;bottom:0;right:0}@-webkit-keyframes load3{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes load3{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.Main-data{display:flex;flex-direction:column}.Main-data h1{font-weight:600;margin:0;display:flex}@media only screen and (max-width: 590px){.Main-data h1{font-size:1.7rem}}@media only screen and (max-width: 450px){.Main-data h1{font-size:1.5rem}}@media only screen and (max-width: 400px){.Main-data h1{font-size:1.3rem}}.Main-data h1 .Svg{text-transform:uppercase;display:flex;align-items:center;margin-left:0.5rem}.Main-data h1 .Svg-span{width:2rem;margin-left:0.5rem}@media only screen and (max-width: 400px){.Main-data h1 .Svg-span{width:1.3rem}}.Main-data h1 .Svg-span svg{width:2rem;height:2rem;fill:#f6c879;-webkit-filter:drop-shadow(2px 1px 10px #f6c879);filter:drop-shadow(2px 1px 10px #f6c879)}@media only screen and (max-width: 400px){.Main-data h1 .Svg-span svg{width:1.3rem;height:1.3rem}}.Main-data h2{font-size:1rem;opacity:0.5;margin:0;font-weight:200}@media only screen and (max-width: 400px){.Main-data h2{font-size:.8rem}}

.button{font-family:'Poppins', sans-serif;border-radius:5px;border:none;font-weight:600;cursor:pointer}.button:active{scale:0.9;transition:0.5s all}

.portal-btn{position:fixed;bottom:1rem;right:1rem;border-radius:1rem;height:3.5rem;width:3.5rem;display:flex;justify-content:center;z-index:90000000;background-color:#f6c879;box-shadow:0px 5px 25px 2px rgba(255,214,129,0.3);transition:0.2s all}@media only screen and (max-width: 400px){.portal-btn{height:3.3rem;width:3.3rem;bottom:0.5rem;right:0.5rem}}

